.uamai__blog-container_article{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    background: var(--color-footer);
}
.uamai__blog-container_article-image{
    width: 100%;
    height: 100%;
    background: var(--color-footer);
}
.uamai__blog-container_article-image img{
    width: 100%;
    height: 100%;
}
.uamai__blog-container_article-content{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1rem 1.5rem;
    height: 100%;
}
.uamai__blog-container_article-content p {
    font-family: var(--font-family);
    font-size: 11.65px;
    font-weight: 700;
    line-height: 35px;

    color: #fff;
}

.uamai__blog-container_article-content h3 {
    font-family: var(--font-family);
    font-size: 25.11px;
    font-weight: 800;
    line-height: 30.30px;
    
    color: #fff;
    margin-bottom: 5rem;
    cursor: pointer;
}

.uamai__blog-container_article-content p:last-child {
    cursor: pointer;
}


@media screen and (max-width: 550px) {
    .uamai__blog-container_article-content h3 {
        font-size: 18px;
        line-height: 25p;
    }
}