.uamai__cta{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    padding: 2rem;
    margin: 4rem;
    border-radius: 1rem;
    background: var(--gradient-bar);
}
.uamai__cta-content p {
    font-size: 12px;
    line-height: 30px;
    font-family: var(--font-family);
    font-weight: 500;
}
.uamai__cta-content h3 {
    font-size: 24px;
    line-height: 45px;
    font-family: var(--font-family);
    font-weight: 800;
}
.uamai__cta-button{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
}
.uamai__cta-button button{
    background: black;
    color: white;
    border-radius: 40px;
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 18px;
    line-height: 30px;
    padding: 0.5rem 1rem;
    border: none;
    outline: none;
    cursor: pointer;
    min-width: 150px;
}
@media screen and (max-width:650px){
    .uamai__cta{
        flex-direction: column;
    }
    .uamai__cta-button{
        margin: 2rem 0 0;
    }
}@media screen and (max-width:550px){
    .uamai__cta{
        margin: 4rem 2rem;
    }
    .uamai__cta-content h3{
        font-size: 14px;
        line-height: 28px;
    }
}