.uamai__header{
    display: flex;
    flex-direction: row;
}
.uamai__header-content{
    flex:1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right:5rem;
}
.uamai__header-content h1{
    font-family: var(--font-family);
    font-size: 40px;
    font-weight: 800;
    line-height: 55px;
    letter-spacing: -0.04rem;
}
.uamai__header-content p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 15px;
    line-height: 20px;
    color: var(--color-text);
    margin-top: 1.5rem;
}
.uamai__header-content__input {
    width: 100%;
    margin: 2rem 0 1rem;
    display: flex;
    flex-direction: row;
}
.uamai__header-content__input input {
    flex: 2;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 15px;
    line-height: 20px;
    background: var(--color-footer);
    border: 2px solid var(--color-footer);
    padding: 0 1rem;
    outline: none;
    color: #fff;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}
.uamai__header-content__input button {
    flex: 0.6;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    background: #FF4820;
    border: 2px solid #FF4820;
    padding: 0 1rem;
    color: #fff;
    cursor: pointer;
    outline: none;

    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}
.uamai__header-content__people {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;
    margin-top: 1.5rem;
}
.uamai__header-content__people img {
    width: 161.79px;
    height: 34px;
}
.uamai__header-content__people p {
    margin: 0rem 0 0 0.8rem;
    line-height: 34px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    color: #fff;
    text-align: center; 
}

.uamai__header-image {
    flex: 0.7;
    display: flex;
    justify-content: center;
    align-items: center;
}

.uamai__header-image img {
    width: 100%;
    height: 100%;
}

@media screen and (max-width:1050px){
    .uamai__header{
        flex-direction: column;
    }
    .uamai__header-content{
        margin: 0 0 1rem;
    }
}
@media screen and (max-width:650px){
    .uamai__header-content h1{
        font-size: 38px;
        line-height: 50px;
    }
    .uamai__header-content p{
        font-size: 15px;
        line-height: 22px;
    }
    .uamai__header-content__people {
        flex-direction: column;
    }
    .uamai__header-content__people p{
        margin-top: 1rem;
    }
    .uamai__header-content__input input, .uamai__header-content__input button{
        font-size: 15px;
        line-height: 22px;
    }
}
@media screen and (max-width:490px){
    .uamai__header-content h1{
        font-size: 32px;
        line-height: 40px;
    }
    .uamai__header-content p{
        font-size: 12px;
        line-height: 18px;
    }
    .uamai__header-content__people {
        flex-direction: column;
    }
    .uamai__header-content__people p{
        margin-top: 1rem;
    }
    .uamai__header-content__input input, .uamai__header-content__input button{
        font-size: 12px;
        line-height: 18px;
    }
}