.uamai__navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.5rem 5rem;
}
.uamai__navbar-links{
    flex:1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.uamai__navbar-links-logo{
    margin-right: 1.5rem;
}
.uamai__navbar-links-container{
    display: flex;
    flex-direction: row;
}
.uamai__navbar-sign{
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.uamai__navbar-links-container p, .uamai__navbar-sign p, .uamai__navbar-menu_container p{
    color:white;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}
.uamai__navbar-sign button, .uamai__navbar-menu_container button{
width: 100px;
font-weight: 500;
color: white;
padding: 0.9rem 0.5rem;
background-color: orangered;
font-family: var(--font-family);
border-radius: 2px;
line-height: 15px;
border: 1px solid orangered;
}
.uamai__navbar-menu{
    margin-left: 1rem;
    display: none;
    position: relative;
}
.uamai__navbar-menu_container{
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        flex-direction: column;
        text-align: end;
        background: var(--color-footer);
        padding: 1.5rem;
        position: absolute;
        right: 0;
        top: 40px;
        margin-top: 1rem;
        min-width: 210px;
        border-radius: 3px;
        box-shadow: 0 0 5 rgba(0, 0,0,0.2);
}
.uamai__navbar-menu_container p{
    margin: 0.9rem 0;
}
.uamai__navbar-menu_container-links-sign{
    display: none;
}
@media screen  and (max-width:1150px){
    .uamai__navbar-links-container{
        display: none;
    }
    .uamai__navbar-menu{
        display: flex;
    }
}
@media screen  and (max-width:700px){
    .uamai__navbar{
        padding:2rem 4rem;
    }
    .uamai__navbar-sign{
        display: none;
    }
    .uamai__navbar-menu_container-links-sign{
        display: block;
    }
}